import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"
import posed from "react-pose"
import {Link} from 'gatsby'
import img from "../images/karlanders_logo_blue.svg"

import MenuTop from '../components/menutop'

const Logo = styled.div`
  position: fixed;
  width: 200px;
  right: -190px;
  bottom: 0;
  margin-bottom: 25px;
  transform-origin: bottom left;
  transform: rotateZ(-90deg);
  z-index: 998;

  @media(min-width: 992px) {
    position: fixed;
    width: 300px;
    right: -280px;
    bottom: 0;
    margin-bottom: 25px;
    transform-origin: bottom left;
    transform: rotateZ(-90deg);
    z-index: 999;
  }

  img {
    height: 100%;
    width: 100%
  }
`

const Transition = posed.div({
  enter: {
    opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      opacity: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})

const Information = styled.div`
  background-color: ${props => props.color};
  font-family: 'Calibre-Regular';
  color: white;
  height: 100%;
  position: relative;
  z-index: 999;

  @media(min-width: 992px) {
    height: 50vh;
  }
`
const Title = styled.h1`
  font-size: 48px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
`

const Text = styled.p`
  font-size: 24px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
  max-width: 800px;
`

const Page = ({ data: { prismicPage } }) => {

  const { data } = prismicPage;

  var itemsPre = function(prismicPage) {
    this.name = prismicPage.data.title.text;
    this.tags = [];
    prismicPage.tags.map(tag => {return this.tags.push(tag)});
  }

  var items = new itemsPre(prismicPage);

  console.log(items);
  console.log(data.image_gallery);
  

  return (
    <Transition style={{zIndex: 997}}>
    <MenuTop/>
         <Logo>
        <Link to='/ziegertreport/'>
          <img alt="" src={img}/>
        </Link>
      </Logo>
      {data.image_gallery.map((image, index) => (
        <div key={index}>
          <img key={index} alt="" src={image.image.url}/>
        </div>
        )
      )}
      <Information color={`${prismicPage.data.color}`}>
        <Title>{items.name}</Title>
        <Text>{data.text.text}</Text>
      </Information>
    </Transition>
  )
}

export default Page

export const pageQuery = graphql`
  query PostBySlug($uid: String!) {
    prismicPage(uid: { eq: $uid }) {
      tags
      uid
      data {
        title {
          text
        }
        text {
          text
        }
        color
        image_gallery {
          image {
            alt
            copyright
            url
          }
        }
      }
    }
  }
`
